* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#wrapper {
  perspective: 10px;
  height: 100vh;
  overflow-y: auto;
}

.container {
  transform-style: preserve-3d;
  z-index: -1;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
  position: relative;
}

.background {
  transform: translateZ(-40px)scale(5);
}

.foreground {
  transform: translateZ(-20px)scale(3);
}

.background, .foreground {
  z-index: -1;
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}

h1 {
  letter-spacing: 4px;
  color: #fff;
  text-shadow: 0 0 10px #0000004d;
  font-size: 10rem;
  position: relative;
  top: 15rem;
}

.text {
  margin: 5rem 0;
  padding: 0 10rem;
  font-size: 1.5rem;
}

section {
  color: #fff;
  background-color: #2d2d2d;
  padding: 5rem 0;
}

.disc {
  color: #000;
  font-size: 3.5rem;
  font: weight 600px;
  background-color: #fff;
  padding: .5rem 1rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-40%)translateY(-40%);
}

.head {
  padding: 0 10rem;
  font-size: 3rem;
}

.bg {
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  height: 500px;
  position: relative;
}

